<template lang="en">
<div class="my-popups">
    <div class="modal d-block ">
        <div class="modal-dialog modal-dialog-centered ui-draggable">
            <div class="modal-content">

                <div class="modal-header ui-draggable-handle  py-0">
                    <button type="button" class="close" @click="closePopup">
                        <em aria-hidden="true" class="fa fa-times"></em>
                    </button>
                    <h4 name="header" class="modal-title py-3">
                        {{ $t('CustomFieldDependency') }} {{SelectedCustomFieldName}}
                    </h4>

                </div>
                <div class="listing py-3 px-3">
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                        <template v-slot:tgslot-ProductTypeCode="{ data }" v-if="DependencyData != null">
                            <div class="col-lg-12 p-0 workdiary_sanp_row border p-3">
                                <div class="timeline-panel row">
                                    <!-- <ul v-for="item in GetMultiSelectValue" :key="index">

                                            <li>{{item.label}}</li>
                                        </ul> -->
                                    <div class="col-md-6">
                                        <ul class="list-group">
                                            <li class="list-group-item" v-for="(item, index) in GetMultiSelectValue" :key="index" @click="showWhenSelectItem(item)" :class="{ sel: selectedItem && selectedItem.name === item.name }">
                                                {{ item.name }}
                                            </li>
                                        </ul>
                                    </div>
                                    <div class="col-md-6 border-left pl-5">
                                        <div class="scroldv">
                                            <ul class="list-group " id="rightList">
                                                
                                                <li class="list-group-item" v-for="itm in Dependencyvalues">

                                                    <label class="form-check form-check-inline">
                                                        <div class="custom-control custom-checkbox custom-control-inline">

                                                            <input @change="checkedSelected(itm)" :id="itm.value" type="checkbox" class="chkItems custom-control-input" >
                                                            <label :for="itm.value" class="custom-control-label">{{itm.name}}</label>
                                                            <input type="hidden" :value="itm.value">
                                                        </div>
                                                    </label>

                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </template>
                    </dynamic-form>
                </div>

            </div>
        </div>
    </div>
</div>
</template>

<script>
