import DataService from "../../services/DataService.js";

export default {
    components: {
        DynamicForm
    },
    props: {
        CustomFieldId: {
            type: Number,
            required: true
        },
        DependencyData: {
            type: Array,
            required: true
        },
        CustomLayoutId:{
            type: Number,
            required: true
        },
        Dependencyvalues: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            dependentId: null,
            selectedItem: null,
            dependentOnDdlId: '',
            type: 'dependonddl',
            CustomData: [],
            dependentListArray: [],
            CustomArray: [],
            SelectedCustomFieldName: null,
            GetMultiSelectValue: [],
            GetEditDependencyData: [],
            showWhenListSelectedItem: '',
            TagsSelectedArray: [],
            DepartmentData:[],
            FormSchema: [{
                layoutType: "Single",
                Data: [{
                    astype: "SelectField",
                    label: this.$t('DependOn'),
                    name: "DependOn",
                    value: "",
                    config: {
                        options: [],
                        onChange: this.GetCustomFieldDDL
                    },
                    validationRules: "required",
                }

                ],
            },
            {
                layoutType: "Single",
                Data: [{
                    astype: "MultiSelectField",
                    label: this.$t('ShowWhen'),
                    name: "ShowWhen",
                    value: '',
                    mode: "tag",
                    placeholder: "",
                    config: {
                        options: [],
                        onChange: this.GetMultiValues,
                        onRemove: this.removeRecord,
                    },
                    //validationRules: "required",
                },

                ]
            },
            {
                layoutType: "Single",
                Data: [{
                    astype: "SlotField",
                    label: '',
                    name: "ProductTypeCode",
                    value: "",
                    placeholder: "",
                    config: {
                        options: [],
                    },
                }]
            },
            ],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save"
            },

            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.closePopup
            }
            ]
        }

    },
    created: function () {
        var vm = this;
        //debugger
        vm.SelectedCustomFieldName = vm.$parent.customFieldName;
        vm.GetCustomFieldDependent();
        vm.editDependency();
    },
    methods: {
        GetCustomFieldDependent() {
            var vm = this;
            (vm.FormSchema)[0].Data[0].config.options = [];
            vm.DependencyData.forEach(item => {
                (vm.FormSchema)[0].Data[0].config.options.push({
                    value: item.custom_field_id,
                    name: item.display_name
                })
            })
        },
    //    GetCheckBoxDependentOn() {
        //     var vm = this;
        //     //debugger
        //     //var DepartmentData = [];
        //     var params = `customFieldId=${vm.CustomFieldId}`
