        //         vm.DepartmentData = JSON.parse(response.data);
        //         console.log("departmentData", DepartmentData);

        //         if (vm.DepartmentData.data != null) {

        //             // Applied these conditions according to dharmpal sir

        //             vm.DepartmentData.data.forEach(item => {
        //                 const option = {
        //                     value: item.custom_field_id,
        //                     name: ""
        //                 };

        //                 if (item.display_name && item.display_name !== "") {
        //                     for (const [key, value] of Object.entries(EN)) {
        //                         if (key == item.display_name) {
        //  //                             option.name = value;
        //                             break; // Break the loop once a match is found
        //                         }
        //                     }
        //                     if (option.name === "") {
        //                         // If no match is found in EN, use item.display_name
        //                         option.name = item.display_name;
        //                     }
        //                 } else {
        //                     for (const [key, value] of Object.entries(EN)) {
        //                         if (key == item.name) {
        //                             option.name = value;
        //                             break; // Break the loop once a match is found
        //                         }
        //                     }
        //                     if (option.name === "") {
        //                         // If no match is found in EN, use item.name
        //                         option.name = item.name;
        //                     }
        //                 }

        //                 (vm.FormSchema)[0].Data[0].config.options.push(option);
        //             });
        //         }

        //     })

        // },
        async GetCustomFieldDDL(e, field, dependentId) {
            var vm = this;
            var CustomData = [];
            if (dependentId) {
                vm.dependentOnDdlId = dependentId;
            } else {
                vm.dependentOnDdlId = e.target.value;
            }
            (vm.FormSchema)[1].Data[0].config.options = [];
            var params = `customFieldId=${vm.dependentOnDdlId}&Type=${vm.type}`;
            await DataService.GetCustomFieldShowWhenDDL(params).then((response) => {
                CustomData = JSON.parse(response.data);
                if (CustomData != null) {
                    CustomData.forEach(item => {
                        (vm.FormSchema)[1].Data[0].config.options.push({
                            value: item.value,
                            name: item.name
                        })
                    })
                }
                if (e != "") {
                    if (e.target.value == vm.dependentId) {
                        vm.editDependency();
                    }
                }
                vm.TagsSelectedArray = [];
                vm.GetMultiSelectValue = [];
                (vm.FormSchema)[1].Data[0].value = [];
                $('#rightList input:checkbox').each(function (index, item) {
                    $(item).prop('checked', false);
                });
            })
        },
        closePopup() {
            var vm = this;
            vm.$parent.isShowDependencyFieldPopup = false;
            vm.$parent.popupcloseforsystemfields();
        },
        showWhenSelectItem(itm) {
            var vm = this;
            var GetDependencyDataArray = [];
            GetDependencyDataArray = JSON.parse(vm.GetEditDependencyData.value_mapping);
            var ele=document.getElementsByClassName("chkItems custom-control-input ");
            for(var j=0; j<ele.length; j++){
                ele[j].checked = false;
            }
            GetDependencyDataArray.forEach(item=>{
                if(item.value == itm.value){
                    item.dependent.forEach(inneritem=>{
                        for(var i=0; i<ele.length; i++){ 
                            if(ele[i].id == inneritem){
                                ele[i].checked = true;
                            }
                        }
                    })
                }
            })
            vm.selectedItem = itm;
            // $('#rightList :checked').each(function (index, item) {
            //     $(item).prop('checked', false);
            // });

            // if (vm.dependentListArray.length > 0) {
            //     for (var i = 0; i < vm.dependentListArray.length; i++) {
            //         if (itm.value == vm.dependentListArray[i].value) {
            //             vm.dependentListArray[i].dependent.forEach(itmVal => {
            //                 $('#rightList input:checkbox').each(function (index, item) {
            //                     if ($(item).siblings('input[type="hidden"]').val() == itmVal) {
            //                         $(item).prop('checked', true);
            //                     }
            //                 });

            //             })
            //         }
            //     }
            // }

            // vm.showWhenListSelectedItem = itm.value;
            // if (vm.dependentListArray.filter(x => x.value == itm.value).length == 0) {
            //     var dependentValueObj = {
            //         "value": itm.value,
            //         "dependent": []
            //     }
            //     vm.dependentListArray.push(dependentValueObj);
            // }
            // if (JSON.parse(vm.GetEditDependencyData.value_mapping).length > 0) {
            //     for (var i = 0; i < vm.dependentListArray.length; i++) {
            //         if (itm.value == vm.dependentListArray[i].value) {
            //             JSON.parse(vm.GetEditDependencyData.value_mapping).forEach(itmVal => {
            //                 if (itmVal.value == itm.value) {
            //                     itmVal.dependent.forEach(itemValue => {
            //                         $('#rightList input:checkbox').each(function (index, item) {
            //                             if ($(item).siblings('input[type="hidden"]').val() == itemValue) {
            //                                 $(item).prop('checked', true);
            //                             }
            //                         });
            //                     });
            //                 }
            //             })
            //         }
            //     }
            // }

            // if (vm.dependentListArray.length > 0) {
            //     for (var i = 0; i < vm.dependentListArray.length; i++) {
            //         if (vm.dependentListArray[i].value == itm.value) {
            //             vm.dependentListArray[i].dependent = [];
            //             $('#rightList :checked').each(function (index, item) {
            //                 vm.dependentListArray[i].dependent.push($(item).siblings('input[type="hidden"]').val());
            //             });
            //         }
            //     }
            // }
        },
        checkedSelected(value) {
            var vm = this;
            debugger
            if (vm.dependentListArray.length > 0) {
                for (var i = 0; i < vm.dependentListArray.length; i++) {
                    if (vm.dependentListArray[i].value == vm.showWhenListSelectedItem) {
                        vm.dependentListArray[i].dependent = [];
                        $('#rightList :checked').each(function (index, item) {
                            vm.dependentListArray[i].dependent.push($(item).siblings('input[type="hidden"]').val());
                        });
                    }
                }
            }
        },
        removeRecord(event, field, meta) {
            //debugger
            var vm = this;
            let getindex = vm.GetMultiSelectValue.findIndex(x => x.value === event.value);
            if (getindex != -1) {
                vm.GetMultiSelectValue.splice(getindex, 1);
            }
        },
        GetMultiValues() {
            var vm = this;
            var obj = (vm.FormSchema)[1].Data[0].config.options.filter(x => x.name == event.target.textContent)[0];
            obj.isSel = false;
            vm.GetMultiSelectValue.push(obj);
        },
        onSubmit(formfields, e) {
            var vm = this;
            var dependentOn = formfields.DependOn;
            var showWhenArray = [];
            var showWhen = JSON.stringify(Array.prototype.map.call(formfields.ShowWhen, function (item) {
                showWhenArray.push(item.value);
            }))
            var obj = {
                "depend_on": formfields.DependOn,
                "value": showWhenArray
            };
            var url = `customFieldDependencyId=&customlayoutId=${vm.CustomLayoutId}&customFieldId=${vm.CustomFieldId}&dependentOn=${dependentOn}&showWhen=${JSON.stringify(obj)}&valueMapping=${JSON.stringify(vm.dependentListArray)}`
            DataService.ManageCustomFieldDependency(url).then(response => {
                if (response.data == '1') {
                    vm.ShowAlert(vm.$t('DependentSuccess'), "Success", true, vm.$t("Alert"));
                    vm.closePopup();
                } else if (response.data == '2') {
                    vm.ShowAlert(vm.$t('UpdatedSuccessfully'), "Success", true, vm.$t("Alert"));
                    vm.closePopup();
                }
            })
        },
        async editDependency() {
            var vm = this;
            vm.GetEditDependencyData = [];
            var dependentData = [];
            vm.TagsSelectedArray = [];
            vm.GetMultiSelectValue = [];
            (vm.FormSchema)[1].Data[0].value = [];
            var url = `CustomLayoutId=${vm.$parent.CustomLayoutId}&customFieldId=${vm.$parent.CustomFieldId}&dependentOnId=`; //${vm.dependentOnDdlId}`
            await DataService.GetCustomFieldId(url).then(response => {
                dependentData = JSON.parse(response.data)
                if (dependentData.data.length > 0) {
                    vm.GetEditDependencyData = dependentData.data[0];

                }
            })
            vm.dependentId = dependentData.data[0]?.dependent_on ?? null;
            if (vm.dependentId != null) {
                //
                (vm.FormSchema)[0].Data[0].value = vm.dependentId;
                await vm.GetCustomFieldDDL('', '', vm.dependentId);
                const showWhenValues = JSON.parse(vm.GetEditDependencyData.show_when).value;
                const selectedValues = (vm.FormSchema)[1].Data[0].config.options.filter(item => {
                    return showWhenValues.includes(item.value);
                });

                vm.GetMultiSelectValue = selectedValues;
                vm.TagsSelectedArray = selectedValues.map(item => ({
                    label: item.name,
                    value: item.value
                }));
                (vm.FormSchema)[1].Data[0].value = vm.TagsSelectedArray;
                return;
            } else {

                return 
            }

        }
    }
}
</script>

<style scoped>
.scroldv {
    max-height: 490px;
    overflow-y: scroll;
}

.sel {
    background-color: #41b883;
    /* Add your selected item style */
    color: #fff;
}

.timeline-panel li {
    max-width: 95%;
    min-width: 95%;
    margin-bottom: 0;
}

.custom-checkbox .custom-control-label {
    margin-top: 5px;
    margin-left: 0px;
    padding-top: 3px;
    padding-left: 5px;
}

.custom-control-input {
    z-index: 1;
    opacity: 0;
    top: 5px;
}
</style>
