                            <!-- <div class="col-lg-12 p-0 text-right my-3 float-left">
                                <a href="javascript:;" v-bind:class="{ 'btn btn-success mr-2': true }"
                                    v-on:click="SubmitData()" title="Save"><em class="fa fa-save pr-2"></em>Submit</a>
                                <button class="btn btn-danger formbtn widthhalf" title="Cancel"
                                    @click="CloseCustomFieldsPopup"><em class="fa fa-close pr-2"></em>Cancel</button>
                            </div> -->
                        </div>
